<template>
    <div class="conteiner">
        <div class="error-message" :class="isshow_error ? 'error-message-visable': 'error-message-hidden'">{{error}}</div>
        <div class="info-head">
            <div class="left-head">
                <div class="login">
                    {{ login }}
                    <span v-if="proxy">proxy: {{ proxy }} <span :class="proxy_status ? 'proxy_active' : 'proxy_broken'"></span></span>
                </div>
                <router-link class="edit-button" :to="'/settings?account='+id">
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><title>Edit</title><path d="M21.7,13.35L20.7,14.35L18.65,12.3L19.65,11.3C19.86,11.09 20.21,11.09 20.42,11.3L21.7,12.58C21.91,12.79 21.91,13.14 21.7,13.35M12,18.94L18.06,12.88L20.11,14.93L14.06,21H12V18.94M12,14C7.58,14 4,15.79 4,18V20H10V18.11L14,14.11C13.34,14.03 12.67,14 12,14M12,4A4,4 0 0,0 8,8A4,4 0 0,0 12,12A4,4 0 0,0 16,8A4,4 0 0,0 12,4Z" /></svg>
                </router-link>
            </div>
            <div class="right-head">
                <div class="qr-code" title="Copy Steam QR-code screenshot and click here" @click="copy_qr()">
                    <span v-if="qr_status == 0">qr</span>
                    <Loader v-else-if="qr_status == 1" :size="'20px'" :border="'1px'"></Loader>
                    <span v-else-if="qr_status == 2" class="qr-success"></span>
                    <span v-else class="qr-unsuccess"></span>
                </div>
                <div class="code" @click="copy_code()">
                    {{ code }}
                    <CodeTimer></CodeTimer>
                    <div v-if="show_copied" class="v-message">Copied to clipboard</div>
                </div>
            </div>
        </div>
        <div v-if="token_valid" class="info-conf-header">
            <SwitchBox :left="false" :checked="auto_confirm" @switch="turn_auto_confirmations">Auto-confirmation</SwitchBox>
            <UpdateButtom v-if="!auto_confirm" @click="load_confirmations()"></UpdateButtom>
        </div>

        <!-- Ожидание загрузки -->
        <div v-if="await_confirm && token_valid" class="info-main">
            <Loader :size="'100px'" :border="'5px'" :opt="0.8"></Loader>
        </div>

        <div v-else-if="auto_confirm && token_valid" class="info-main">
            <span>You have the auto-confirmation feature enabled.<br>Turn off auto-confirmation to display a list of confirmations</span>
        </div>

        <div v-else-if="!confirmations.length && token_valid" class="info-main">
            <span>You don't have any active confirmations right now.<br>Click "refresh" to get the latest list</span>
        </div>


        <!-- Вывод подтверждений -->
        <div v-else-if="token_valid" class="confirmations">
            <div class="conf-header">
                <div class="select-all">
                    <CheckBox :checked="selected_all" @change="select_all()"></CheckBox>
                    <span>Select All</span>
                </div>
                <div class="buttons">
                    <button @click="accept()">Accept</button>
                    <button @click="reject()">Reject</button>
                </div>
            </div>
            <div class="conf-body">
                <ConfirmationItem 
                    v-for="confirmation in confirmations" 
                    :key="confirmation.id" 
                    :item="confirmation" 
                    @selected="confirmation.selected = !confirmation.selected"
                    @accept="accept(confirmation.id)"
                    @reject="reject(confirmation.id)">
                </ConfirmationItem>
            </div>
        </div>

        <div v-else class="relogin">
            <div v-if="relogin_error" class="relogin-error">{{ relogin_error }}</div>
            <div class="relogin-body" v-if="!await_relogin">
                <div class="title">Relogin</div>
                <input type="text" placeholder="Password" v-model="password">
                <div class="buttons">
                    <button @click="relogin()">Login</button>
                </div>
            </div>
            <Loader v-else :size="'100px'" :border="'5px'" :opt="0.8"></Loader>
            <div class="relogin-footer">
                It looks like you've been gone for a long time and your session is out of date. Please enter your account password and we will update the session
            </div>
        </div>
    </div>
</template>

<script>
import CodeTimer from '@/components/CodeTimer.vue';
import SwitchBox from "@/components/SwitchBox.vue";
import Loader from "@/components/LoadingAwait.vue";
import ConfirmationItem from '@/components/ConfirmationItem.vue';
import UpdateButtom from '@/components/UpdateButtom.vue';
import CheckBox from '@/components/CheckBox.vue';

export default{
    components:{
        CodeTimer,
        SwitchBox,
        Loader,
        ConfirmationItem,
        UpdateButtom,
        CheckBox
    },
    data(){
        return {
            error: "",
            isshow_error: false,

            update_timestamp: 0,
            update_interval: null,
            login: "",
            auto_confirm: false,
            token_valid: true,
            code: "",
            show_copied: false,
            proxy: '',
            proxy_status: true,
            qr_status: 0,

            confirmations: [],
            await_confirm: false,
            selected_all: false,

            relogin_error: "",
            password: "",
            await_relogin: false
        }
    },
    mounted(){
        this.update_data();
    },
    unmounted(){
        if (this.update_interval) clearTimeout(this.update_interval);
    },
    computed:{
        id(){
            return this.$route.params.id
        },
        confirmations_arr(){
            return this.confirmations.map(val=>{
                val.selected = this.selected_all
                return val
            })
        }
    },
    watch:{
        id(){
            if (this.update_interval) clearTimeout(this.update_interval);
            this.update_data()
            this.confirmations = [];
            //this.load_confirmations();
        }
    },
    methods:{
        async update_data(){
            const account = await this.$message("get-account", {id: this.id})
            this.selected_all = false;
            this.code = account.code;
            this.login = account.login;
            this.auto_confirm = account.auto_confirm
            this.proxy = account.proxy;
            this.proxy_status = account.proxy_status
            this.token_valid = account.token_valid;
            const timeout = account.code_end * 1000 - Date.now();
            if (timeout > 0) this.update_interval = setTimeout(this.update_data, timeout)
            else this.update_data()
        },
        async copy_code(){
            await this.$message("copy-code", {id: this.id})
            this.show_copied = true;
            setTimeout(()=>{
                this.show_copied = false
            }, 1000)
        },
        async copy_qr(){
            if (this.qr_status !== 0) return;
            this.qr_status = 1;
            const result = await this.$message("copy-qr", {id: this.id})
            if (result.success) this.qr_status = 2;
            else {
                this.show_error(result.error)
                this.qr_status = 3;
            }
            setTimeout(()=>{
                this.qr_status = 0;
            }, 3000)
        },
        async turn_auto_confirmations(){
            this.auto_confirm = !this.auto_confirm;
            this.$message("turn-auto-confirmation", {enable: this.auto_confirm, id: this.id})
            if (!this.auto_confirm) this.load_confirmations();
            this.$emit("update")
        },
        async load_confirmations(){
            this.await_confirm = true
            const result = await this.$message("load-confirmations", {id: this.id});
            this.await_confirm = false;
            if (result.success){
                this.confirmations = result.confirmations.map((val)=>{
                    val.selected = this.selected_all;
                    val.await = false;
                    return val;
                })
                console.log(this.confirmations)
            }else this.show_error(result.error)
        },
        select_all(){
            this.selected_all = !this.selected_all
            this.confirmations.forEach(item=>{
                item.selected = this.selected_all
            })
        },
        async respond_confirm(accept = true, id = null){
            const items = id ? [id] : this.confirmations.filter(val=> val.selected).map(val=> val.id);
            if (!items.length) return;
            this.confirmations.forEach(val=>{
                if (items.includes(val.id)) val.await = true;
            })
            const result = await this.$message("respond-confirmations", {items, id: this.id, accept})
            this.confirmations = result.confirmations.map((val)=>{
                val.selected = this.selected_all;
                val.await = false;
                return val;
            })
            console.log(result)
        },
        async accept(id = null){
            this.respond_confirm(true, id);
        },
        async reject(id = null){
            this.respond_confirm(false, id);
        },
        show_error(error){
            this.error = error;
            this.isshow_error = true;
            setTimeout(()=>{
                this.isshow_error = false;
            }, 3000)
        },

        async relogin(){
            this.await_relogin = true;
            const result = await this.$message("relogin", {id: this.id, password: this.password});
            this.await_relogin = false;
            if (result.success){
                this.$emit("update")
                return this.update_data();
            }else {
                this.relogin_error = result.error;
            }
        }
    }
}

</script>

<style scoped>
    .conteiner{
        display: flex;
        padding: 20px 20px 0 20px;
        flex-direction: column;
        width: 560px;
        position: relative;
    }
    .error-message{
        position: absolute;
        display: flex;
        top: 0;
        left: 0;
        font-size: 14px;
        background: rgba(255,0,0, 0.2);
        padding: 5px;
        color: #ddd;
        width: 590px;
        justify-content: center;
    }
    .error-message-hidden{
        visibility: hidden;
        opacity: 0;
        transition: visibility 0s 1s, opacity 1s linear;
    }
    .error-message-visible{
        visibility: visible;
        opacity: 1;
    }
    .info-head{
        height: 80px;
        border-bottom: 1px solid rgba(255,255,255, .05);
        width: 100%;
        display: flex;
        align-content: center;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 20px;
    }
    .left-head{
        display: flex;
        align-items: center;
        gap: 20px;
    }
    .login{
        color: #dddddd;
        text-transform: uppercase;
        font-size: 20px;
        display: flex;
        flex-direction: column;
        cursor: default;
    }
    .login span{
        font-size: 12px;
        text-transform: lowercase;
        display: flex;
        align-items: center;
        gap: 10px;
    }
    .proxy_broken, .proxy_active{
        width: 10px;
        height: 10px;
        border-radius: 10px;
        display: inline-block;
    }
    .proxy_broken{
        background: #c52b2b;
    }
    .proxy_active{
        background: #2bc559;
    }
    .right-head{
        display: flex;
        gap: 20px
    }
    .qr-code{
        display: flex;
        height: 40px;
        width: 40px;
        background: rgba(0,0,0,0.2);
        box-shadow: 0px 3px 6px 1px inset rgba(0,0,0,.15);
        color: #ddd;
        font-size: 18px;
        align-items: center;
        justify-content: center;
        letter-spacing: .05em;
        cursor: pointer;
        border-radius: 4px;
        text-transform: uppercase;
    }
    .qr-success, .qr-unsuccess{
        position: relative;
    }
    .qr-success::before{
        content: " ";
        position: absolute;
        border-top: 2px solid #ddd;
        width: 9px;
        transform: rotate(45deg);
        left: -10px;
        top: 3px;
    }
    .qr-success::after{
        content: " ";
        position: absolute;
        border-top: 2px solid #ddd;
        width: 18px;
        transform: rotate(135deg);
        left: -6px;
        top: 0px;
    }

    .qr-unsuccess::before, .qr-unsuccess::after{
        content: " ";
        position: absolute;
        border-top: 2px solid #ddd;
        width: 20px;
        left: -9px;
        top: 0px;
    }
    .qr-unsuccess::before{
        transform: rotate(135deg);
    }
    .qr-unsuccess::after{
        transform: rotate(45deg);
    }
    .code{
        display: flex;
        height: 40px;
        width: 100px;
        position: relative;
        background: rgba(0,0,0,0.2);
        box-shadow: 0px 3px 6px 1px inset rgba(0,0,0,.15);
        color: #ddd;
        font-size: 22px;
        align-items: center;
        justify-content: center;
        letter-spacing: .05em;
        cursor: pointer;
        border-radius: 4px;
    }

    .info-conf-header{
        display: flex;
        margin: 10px 0;
        line-height: 30px;
        align-items: center;
        justify-content: space-between;
    }
    .info-main{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        flex-grow: 1;
    }

    .v-message{
        position: absolute;
        font-size: 9px;
        top: 45px;
    }
    .confirmations{
        color:#ddd;
    }

    .conf-header{
        display: flex;
        border-bottom: 1px solid rgba(255,255,255, .05);
        height: 40px;
        align-items: center;
        justify-content: space-between;
    }
    .conf-header .select-all{
        display: flex;
        font-size: 12px;
        align-items: center;
        gap: 15px
    }
    .conf-body{
        overflow-y: scroll;
        height: 338px;
    }
    .conf-body::-webkit-scrollbar{
        display: none;
    }

    .info-main span{
        color: rgba(255,255,255, 0.15);
        text-align: center;
        font-size: 20px;
    }

    .edit-button{
        color: #ddd;
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
    }

    .edit-button svg{
        width: 20px;
        filter: invert(100%) sepia(0%) saturate(1509%) hue-rotate(27deg) brightness(83%) contrast(107%);
    }

    .edit-button:hover{
        background: rgba(255,255,255, 0.05);
        border-radius: 50%;
    }

    .relogin{
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding-top: 20px;
        align-items: center;
        height: 409px;
        justify-content: space-evenly;
        position: relative;
    }

    .relogin-error{
        position: absolute;
        top: 10px;
        background: rgba(255,0,0, 0.2);
        border: 1px solid rgba(255,0,0, 0.3);;
        padding: 10px 20px;
        border-radius: 3px;
        color: #ddd;
        font-size: 14px;
    }
    .relogin-body{
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    .relogin-body .title{
        color:#ddd;
        font-size: 26px;
        padding-bottom: 10px;
        text-align: center;
    }
    .relogin-footer{
        color: #626262;
        display: flex;
        text-align: center;
    }

    .relogin-body input{
        width: 300px;
        height: 40px;
        outline: none;
        border: 1px solid rgba(255,255,255, 0.2);
        background: none;
        border-radius: 4px;
        box-sizing: border-box;
        color: #ddd;
        padding: 0 10px;
        letter-spacing: 0.05em;
    }

    .relogin-body input:hover, .relogin-body input:focus{
        background: rgba(255,255,255, 0.05);
    }

    .relogin-body button{
        width: 300px !important;
        height: 40px !important;
        margin: 0 !important;
    }

</style>