<template>
    <div class="update"></div>
</template>

<script>

export default{
    props: ["item"],
    data(){
        return {
            button_color: "#9f9f9f"
        }
    },
}

</script>

<style scoped>
    .update{
        width: 20px;
        height: 20px;
        border: 2px solid v-bind(button_color);
        border-bottom-color: transparent;
        border-radius: 50%;
        display: inline-block;
        box-sizing: border-box;
        transform: rotate(290deg);
        transition: transform 0.1s linear;
        cursor: pointer;
        margin: 5px;
        position: relative;
    }
    .update::before{
        content: " ";
        width: 6px;
        border-top: 2px solid v-bind(button_color);
        position: absolute;
        top: 14px;
        left: 13px;
    }
    .update::after{
        content: " ";
        width: 6px;
        border-top: 2px solid v-bind(button_color);
        position: absolute;
        top: 12px;
        left: 10px;
        transform: rotate(80deg);
    }

    .update:hover{
        transform: rotate(340deg);
    }

</style>