<template>
    <div class="loader_container">
        <span class="loader"></span>
    </div>
</template>

<script>

export default{
    props:{
        size: {
            default: "48px"
        },
        border: {
            default: "5px"
        },
        opt: {
            default: 1
        }
    }
}

</script>

<style scoped>

.loader_container{
    display: flex;
    justify-content: center;
}

.loader {
    width: v-bind(size);
    height: v-bind(size);
    border: v-bind(border) solid rgba(255,255,255, v-bind(opt));
    border-bottom-color: transparent;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
} 
</style>