<template>
    <div class="switchbox">
        <input type="checkbox" :checked="checked" :id="'switch_'+switch_id" @change="$emit('switch')">
        <label :for="'switch_'+switch_id">
            <slot></slot>
            <div class="picker"></div>
        </label>
    </div>
</template>

<script>

export default{
    props:{
        left: {
            default: true
        },
        checked: {
            default: false
        }
    },
    data(){
        return {
            switch_id: Math.round(Math.random() * 10000),
            row: this.left ? 'row-reverse' : 'row'
        }
    },
    methods:{
        show(data){
            console.log(data)
        }
    }
}

</script>

<style scoped>
.switchbox{
    display: flex;
    font-size: 14px;
}
.switchbox>input {
	display: none;
}
input[type="checkbox"]:checked ~ label, 
input[type="checkbox"]:not(:checked) ~ label {
    display: flex;
    color: #ddd;
    position: relative;
    cursor: pointer;
    align-items: center;
    flex-direction: v-bind(row);
}
.switchbox>input+label .picker{
    display: flex;
    position: relative;
}
input[type="checkbox"]:checked ~ label > .picker:before, 
input[type="checkbox"]:not(:checked) ~ label > .picker:before{
    content: " ";
	width: 40px;
	height: 15px;
    border-radius: 30px;
    border: 1px solid rgba(255,255,255,.05);
    margin: 4px;
    transition: all 0s 0.1s linear;
}
input[type="checkbox"]:checked ~ label > .picker:before{
	background: #2997f19f;
}
input[type="checkbox"]:not(:checked) ~ label > .picker:before{
    background: #838383;
}

input[type="checkbox"] ~ label > .picker:after{
    transition: all .1s linear;
}
input[type="checkbox"]:not(:checked) ~ label > .picker:after,
input[type="checkbox"]:checked ~ label > .picker:after{
    content: " ";
	width: 20px;
	height: 20px;
    border-radius: 20px;
    border: 1px solid rgba(255,255,255,.05);
    position: absolute;
    top: 2px;
    left: 4px;
}
input[type="checkbox"]:checked ~ label > .picker:after{
    background: #2996F1;
    transform: translateX(20px);
	box-shadow: -2px 0px 6px rgba(0,0,0,0.2);
}

input[type="checkbox"]:not(:checked) ~ label > .picker:after{
	background: #9e9e9e;
	box-shadow: 2px 0px 6px rgba(0,0,0,0.2);
}

input[type="checkbox"] ~ label > .picker:after:hover{
    background: #fff;
}



</style>