<template>
    <div class="title-bar">
        <div class="title">{{ short_title }} {{ version }}</div>
        <div class="buttons">
            <div class="minimize" title="Minimize" @click="mini()"></div>
            <!--<div class="tray" title="to Tray" @click="tray()"></div>-->
            <div class="close" title="Close" @click="close()"></div>
        </div>
    </div>
</template>

<script>

export default{
    props: ['version', 'short_title'],
    methods:{
        close(){
            this.$message("close");
        },

        tray(){
            this.$message("to-tray");
        },

        mini(){
            this.$message("minimize")
        }
    }
}

</script>

<style scoped>
    .title-bar{
        -webkit-user-select: none;
        display: flex;
        height: 30px;
        width: 800px;
        background: #1483dd;
        justify-content: space-between;
        align-items: center;
        -webkit-app-region: drag;
    }
    .title{
        color: #fff;
        font-size: 12px;
        padding: 0 20px;
    }
    .buttons{
        line-height: 30px;
        -webkit-app-region: no-drag;
    }

    .minimize, .close,.tray{
        width: 50px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        position: relative;
    }

    .minimize:hover, .tray:hover{
        background: rgba(0,0,0,.1);
    }

    .minimize::before{
        content: " ";
        border-bottom: 1px solid #fff;
        width: 12px;
        position: absolute;
    }

    .tray::before, .tray::after{
        content: " ";
        width: 7px;
        border-bottom: 1px solid #fff;
        position: absolute;
    }

    .tray::before{
        transform: rotate(45deg);
        left: 20px
    }
    .tray::after{
        transform: rotate(-45deg);
        left: 25px
    }

    .close:hover{
        background: #e44848;
    }

    .close::after, .close::before{
        content: " ";
        width: 15px;
        border-top: 1px solid #fff;
        position: absolute;
    }
    .close::before{
        transform: rotate(-45deg);
    }
    .close::after{
        transform: rotate(45deg);
    }
</style>