<template>
    <div class="confirmation">
        <div class="conf-info">
            <div v-if="true" class="checkbox-cont"><CheckBox :checked="item.selected" @change="$emit('selected')"></CheckBox></div>
            <div class="conf-img"><img :src="item.icon" alt=""></div>
            <div v-if="item.type == 6" class="title">
                Account recovery - {{item.creator.slice(-5)}}
                <span class="date">{{ date() }}</span>
            </div>
            <div v-else class="title">
                {{item.title}}
                <span class="date">{{ date() }}</span>
            </div>
        </div>
        <div v-if="!item.await" class="conf-buttons">
            <button class="accept" @click="$emit('accept')"></button>
            <button class="reject" @click="$emit('reject')"></button>
        </div>
        <div v-else class="conf-buttons">
            <LoadingAwait :size="'30px'" :border="'2px'" :opt="0.7"></LoadingAwait>
        </div>
    </div>
</template>

<script>
import CheckBox from './CheckBox.vue';
import LoadingAwait from './LoadingAwait.vue';

export default{
    props: ["item", "checked"],
    components: {
        CheckBox,
        LoadingAwait
    },
    data(){
        return {
            button_color: "#9f9f9f"
        }
    },
    methods: {
        date(){
            const _date = new Date(this.item.timestamp);
            const hours = add_zero(_date.getHours())
            const minutes = add_zero(_date.getMinutes())
            const seconds = add_zero(_date.getSeconds());

            const day = add_zero(_date.getDate());
            const mounth = add_zero(_date.getMonth() + 1);
            const year = _date.getFullYear()
            return `${hours}:${minutes}:${seconds} ${day}.${mounth}.${year}`

            function add_zero(num){
                if (num >= 10) return num;
                else return "0"+num;
            }
        }
    }
}

</script>


<style scoped>
    .confirmation{
        display: flex;
        align-items: center;
        border-bottom: 1px solid rgba(255,255,255, 0.05);
        height: 60px;
        justify-content: space-between;
    }
    .conf-info{
        display: flex;
        align-items: center;
        height: 60px;
        overflow: hidden;
    }
    .checkbox-cont{
        width: 40px;
    }
    .conf-img{
        display: flex;
        width: 60px;
        height: 60px;
        align-items: center;
        justify-content: center;
    }
    .conf-img img{
        width: 50px;
        height: 50px;
    }
    .title{
        display: flex;
        flex-direction: column;
        gap: 5px;
        margin-left: 20px;
        overflow: hidden;
        height: 60px;
        justify-content: center;

    }
    .title .date{
        font-size: 12px;
    }

    .conf-buttons{
        display: flex;
        gap: 10px;
    }
    .conf-buttons button{
        background: none;
        border: none;
        padding: 0;
        width: 30px;
        height: 30px;
        cursor: pointer;
        position: relative;
        transition: all 0.2s linear;
        border-radius: 30px;
    }

    .conf-buttons button:hover{
        background: rgba(255,255,255, 0.05);
    }
    .conf-buttons button:active{
        background: rgba(255,255,255, 0.1);
    }
    .conf-buttons .accept::after{
        content: " ";
        position: absolute;
        border-top: 2px solid v-bind(button_color);
        width: 9px;
        transform: rotate(45deg);
        left: 5px;
        top: 18px;
    }
    .conf-buttons .accept::before{
        content: " ";
        position: absolute;
        border-top: 2px solid v-bind(button_color);
        width: 18px;
        transform: rotate(135deg);
        left: 9px;
        top: 15px;
    }

    .conf-buttons .reject::before, .conf-buttons .reject::after{
        content: " ";
        position: absolute;
        border-top: 2px solid v-bind(button_color);
        width: 20px;
        left: 5px;
        top: 14px;
    }
    .conf-buttons .reject::before{
        transform: rotate(135deg);
    }
    .conf-buttons .reject::after{
        transform: rotate(45deg);
    }
</style>