<template>
    <div class="conteiner">
        <LoadingAwait v-if="stage == 0" :size="'100px'"></LoadingAwait>

        <div v-if="stage == 1" class="form">
            <div class="title">Import</div>
            <div class="error-message" v-if="error">{{error}}</div>
            <div class="message">Select one or more .maFiles from SDA directory for import.</div>
            <SwitchBox @switch="skip_dupl = !skip_dupl" :checked="skip_dupl">Skip duplicate accounts</SwitchBox>
            <LoadingAwait v-if="await_file" :size="'40px'"></LoadingAwait>
            <div v-else class="buttons">
                <button @click="select_file()">Select</button>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Cancel</span>
            </div>
        </div>

        <div v-if="stage == 2" class="form">
            <div class="title">Login</div>
            <div v-if="error" class="error-message">{{ error }}</div>
            <div class="message">Account: {{ login }}</div>
            <div class="message">File: {{ file_name }}</div>
            <input type="text" placeholder="Password" v-model="password">
            <SwitchBox @switch="use_proxy = !use_proxy">Proxy (http only)</SwitchBox>
            <input v-if="use_proxy" type="text" placeholder="[login:password@]IP:PORT" v-model="proxy">
            <div class="buttons">
                <button @click="dologin()">Login</button>
            </div>
            <div v-if="select_dir" class="cancel-button" >
                <span @click="next_account()">Skip account</span>
            </div>
            <div class="cancel-button">
                <span @click="back()">Back</span>
            </div>
        </div>

        <div v-if="stage == 3" class="form">
            <div class="title">Done</div>
            <div class="buttons">
                <button @click="more()">import more</button>
            </div>
            <div class="buttons">
                <button @click="exit()">exit</button>
            </div>
        </div>

        <div v-if="stage == 4" class="form">
            <div class="title">Error</div>
            <div class="error-message">{{ error }}</div>
            <div class="message">File: {{ file_name }}</div>
            <div v-if="accounts.length" class="buttons">
                <button @click="next_account()">Next</button>
            </div>
            <div class="cancel-button">
                <span @click="back()">Back</span>
            </div>
        </div>

        <div v-if="stage == 5" class="form">
            <div class="title">success</div>
            <div class="message" style="text-align: center;">Account '{{ login }}' successfully imported</div>
        </div>
    </div>
</template>


<script>
import SwitchBox from '@/components/SwitchBox.vue';
import LoadingAwait from '@/components/LoadingAwait.vue';

export default{
    components:{
        SwitchBox,
        LoadingAwait
    },
    data(){
        return {
            stage: 1,
            accounts: [],

            await_file: false,
            error: "",
            file_path: "",
            file_pathes: [],
            skip_dupl: true,
            select_dir: false,

            file_name: "",
            login: "",
            password: "",
            use_proxy: false,
            proxy: ""
        }
    },
    methods:{
        async select_file(){
            this.await_file = true;
            const file = await this.$message("import");
            this.file_pathes = file.path;
            this.await_file = false;
            this.error = "";
            if (!this.file_pathes.length) return;
            this.import_file()
        },
        async import_file(){
            this.stage = 0;
            const result = await this.$message("cont-import", {path: this.file_pathes, skip_dupl: this.skip_dupl});
            if (!result.length){
                this.stage = 1
                this.error = "No .maFile files found"
                return;
            }
            this.accounts = result;
            this.next_account();
        },
        next_account(){
            this.error = "";
            if (!this.accounts.length){
                this.stage = 3
                return;
            }
            const account = this.accounts.splice(0, 1)[0];
            this.password = "";
            this.file_name = account.file_name;
            if (account.success){
                this.login = account.login;
                this.stage = 2;
            }else {
                this.error = account.error;
                this.stage = 4;
            }
            
        },
        async dologin(){
            const options = {login: this.login, password: this.password}
            this.stage = 0;
            if (this.use_proxy) options.proxy = this.proxy;
            const result = await this.$message("import-login", options);
            if (!result.success){
                this.error = result.error;
                this.stage = 2;
            }else{ 
                this.stage = 5;
                await new Promise(res=>{
                    setTimeout(res, 1000)
                })
                this.next_account();
            }

        },
        back(){
            this.error = "";
            if (this.stage == 1) this.$router.push({path: '/'})
            else this.stage = 1;
        },
        change_path_type(){
            this.file_path = "";
            this.select_dir = !this.select_dir;
        },
        exit(){
            this.$router.back();
        },
        more(){
            this.stage = 1;
            this.accounts = [];

            this.await_file = false;
            this.error = "";
            this.file_path = "";
            this.skip_dupl = true;
            this.select_dir = false;

            this.file_name = "";
            this.login = "";
            this.password = "";
            this.use_proxy = false;
            this.proxy = "";
        }
    }
}

</script>

<style scoped>
    .conteiner{
        display: flex;
        width: 800px;
        height: 550px;
        justify-content: center;
        align-items: center;
    }
    .form{
        display: flex;
        padding: 40px;
        background: rgba(255,255,255, 0.05);
        box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.2);
        flex-direction: column;
        gap: 20px;
    }
    .title{
        text-transform: uppercase;
        color: #ddd;
        font-size: 24px;
        text-align: center;
        letter-spacing: 0.05em;
        width: 300px;
    }
    .form input{
        width: 300px;
        height: 40px;
        outline: none;
        border: 1px solid rgba(255,255,255, 0.2);
        background: none;
        border-radius: 4px;
        box-sizing: border-box;
        color: #ddd;
        padding: 0 10px;
        letter-spacing: 0.05em;
    }

    .form input:hover, .form input:focus{
        background: rgba(255,255,255, 0.05);
    }

    .form button{
        width: 300px !important;
        height: 40px !important;
        margin: 0 !important;
    }

    .form .cancel-button{
        text-align: center;
        text-decoration: underline;
        color: #ddd;
        font-size: 14px;
    }

    .form .cancel-button span{
        cursor: pointer;
    }

    .form .error-message{
        background: rgba(215, 67, 67, 0.2);
        width: 290px;
        color: #fff;
        font-size: 14px;
        padding: 4px;
        border: 1px solid rgba(215, 67, 67, 0.2);
        border-radius: 4px;
        text-align: center;
    }

    .form .message{
        width: 300px;
        color: rgba(255,255,255, 0.7);
        font-size: 12px;
    }
</style>