<template>
    <div class="left-bar">
        <div class="list-fix">
            <div v-if="accounts.length" class="list">
                <AccountInList v-for="(account, index) in account_list" :key="index" :account="account" :search="search"/>
            </div>
            <div v-else class="empty-list">
                <div class="icon">
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><title>Add or Import accounts</title><path d="M12,20C7.59,20 4,16.41 4,12C4,7.59 7.59,4 12,4C16.41,4 20,7.59 20,12C20,16.41 16.41,20 12,20M12,2A10,10 0 0,0 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2M13,7H11V11H7V13H11V17H13V13H17V11H13V7Z" /></svg>
                </div>
                <div class="desc">Add new or import an account</div>
            </div>
        </div>
        <div class="footer">
            <input type="text" class="search" placeholder="Search" v-model="search">
        </div>
    </div>
</template>

<script>
import AccountInList from './AccountInList.vue'

export default{
    name: "LeftBar",
    components: {
        AccountInList
    },
    data(){
        return {
            accounts: [],
            active: -1,
            search: "",
            update_interval: null
        }
    },
    async mounted(){
        this.update_data();
        this.update_interval = setInterval(this.update_data, 10000)
    },
    unmounted(){
        if (this.update_interval) clearInterval(this.update_interval)
    },
    computed:{
        account_list(){
            return this.accounts.filter(val=> val.login.includes(this.search))
        }
    },
    methods: {
        async update_data(){
            this.accounts = await this.$message("getlist")
            return ""
        }
    }
}

</script>

<style scoped>
    .left-bar{
        background: rgba(0,0,0,0.2);
        width: 200px;
        height: 550px;
        box-shadow: -3px 0px 6px 0px rgba(0, 0, 0, 0.2) inset;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .list-fix{
        overflow-y: scroll;
        height: 500px;
        position: relative;
    }
    .list-fix::-webkit-scrollbar{
        display: none;
    }

    .empty-list{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        color: rgba(255, 255, 255, 0.15);
        cursor: default;
        user-select: none;
        gap:10px
    }

    .empty-list .icon{
        width: 100px;
        height: 100px;
        fill: rgba(255, 255, 255, 0.05)
    }
    .empty-list .desc{
        text-align: center;
    }
    .list{
        display: flex;
        flex-direction: column;
    }
    .footer{
        display: flex;
        height: 50px;
        width: 100%;
        border-top: 1px solid rgba(255,255,255, 0.05);
        align-items: center;
        justify-content: center;
    }
    .search{
        color: rgba(255,255,255, 0.7);
        background: rgba(0,0,0,0.2);
        border: 1px solid rgba(255,255,255,0.1);
        border-radius: 3px;
        height: 30px;
        width: 180px;
        display: flex;
        box-sizing: border-box;
        padding: 0 10px;
    }
    .search:hover, .search:focus{
        background: rgba(255,255,255,0.05);
        border: none;
        padding: 0 11px;
    }
    
</style>