</template>

<script>
import Header from './components/HeaderMenu.vue'
import TitleBar from '@/components/TitleBar.vue'

export default {
  data(){
    return{
      title: "Steam Auth Tool",
      version: "v 1.0",
      short_title: "SAT"
    }
  },
  components:{
    Header,
    TitleBar
  },
  async created(){
    //this.$router.push({path: "/id/76561198216165591"})
    const res = await this.$message("app");
    if (!res.title) return
    this.meta
    this.title = res.title
    this.version = res.version
    this.short_title = res.short_title
    document.title = `${this.title}`
  }
}
</script>

