import { createApp } from 'vue'
import App from './App.vue';
import router from './router';
import './registerServiceWorker'

const transportPlugin = {
    install: (app) => {
        app.config.globalProperties.$message = async (method, data = "") => {
            return await fetch(`node-sda://message.get/${method}`, {method: "POST", body: JSON.stringify(data)}).then(res=> res.json())
        }
    } 
}

const app = createApp(App)
app.use(router)
app.use(transportPlugin)
app.mount('#app')