<template>
    <div class="conteiner">
        <div v-if="id" class="form" @keypress.enter="save_settings()">
            <div class="title">Settings</div>
            <SwitchBox @switch="auto_confirm = !auto_confirm" :checked="auto_confirm">Auto Confirmation</SwitchBox>
            <div class="message">
                Proxy (http only) 
            </div>
            <div class="proxy-input">
                <input :class="proxy_status == 1 ? 'proxy-active' : proxy_status == 2 ? 'proxy-broken' : ''" type="text" v-model="proxy" placeholder="[login:password@]IP:PORT">
                <LoadingAwait v-if="await_proxy" :size="'20px'" :border="'1px'" style="margin-left: 5px;"></LoadingAwait>
                <UpdateButtom v-else @click="check_proxy()" title="Check Proxy"></UpdateButtom>
            </div>
            
            <div class="buttons">
                <button @click="save_settings()">save</button>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Cancel</span>
            </div>
        </div>

        <div v-else class="form" @keypress.enter="save_settings()">
            <div class="title">Settings</div>
            <SwitchBox @switch="auto_confirm = !auto_confirm" :checked="auto_confirm">Auto Confirmation</SwitchBox>
            <div class="message">Confirmation Interval, sec</div>
            <input type="number" v-model="confirm_interval">
            <div class="buttons">
                <button @click="save_settings()">save</button>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Cancel</span>
            </div>
        </div>
    </div>
</template>

<script>
import SwitchBox from '@/components/SwitchBox.vue';
import UpdateButtom from '@/components/UpdateButtom.vue';
import LoadingAwait from '@/components/LoadingAwait.vue';

export default{
    components:{
        SwitchBox,
        UpdateButtom,
        LoadingAwait
    },
    data(){
        return {
            auto_confirm: false,
            confirm_interval: 30,
            id: this.$route.query.account ?? null,

            proxy: "",
            proxy_status: 0,
            await_proxy: false
        }
    },
    async mounted(){
        if (this.id){
            const result = await this.$message('get-config', {id: this.id});
            this.auto_confirm = result.auto_confirm;
            this.proxy = result.proxy ? result.proxy.replace(/https?:\/\//, "") : null
        }else{
            const result = await this.$message('get-config');
            this.auto_confirm = result.auto_confirm;
            this.confirm_interval = result.confirm_interval;
        }
    },
    methods:{
        async check_proxy(){
            if (!this.proxy) return;
            this.await_proxy = true
            const {success} = await this.$message("check-proxy", {proxy: this.proxy})
            this.await_proxy = false

            this.proxy_status = success  ? 1 : 2;
            await new Promise(res=>{
                setTimeout(res, 3000);
            })
            this.proxy_status = 0;
        },
        async save_settings(){
            if (!this.id){
                await this.$message('set-config', {auto_confirm: this.auto_confirm, confirm_interval: this.confirm_interval})
            }else await this.$message('set-config', {id: this.id, auto_confirm: this.auto_confirm, proxy: this.proxy})
            
            this.back()
        },
        back(){
            if (!this.id) this.$router.push({path: "/"})
            else this.$router.back();
        }
    }
}

</script>

<style scoped>
    .conteiner{
        display: flex;
        width: 800px;
        height: 550px;
        justify-content: center;
        align-items: center;
    }
    .form{
        display: flex;
        padding: 40px;
        background: rgba(255,255,255, 0.05);
        box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.2);
        flex-direction: column;
        gap: 20px;
    }
    .title{
        text-transform: uppercase;
        color: #ddd;
        font-size: 24px;
        text-align: center;
        letter-spacing: 0.05em;
        width: 300px;
    }
    .form input[type="number"]::-webkit-outer-spin-button,
    .form input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
    .form input{
        width: 300px;
        height: 40px;
        outline: none;
        border: 1px solid rgba(255,255,255, 0.2);
        background: none;
        border-radius: 4px;
        box-sizing: border-box;
        color: #ddd;
        padding: 0 10px;
        letter-spacing: 0.05em;
    }

    .form input:hover, .form input:focus{
        background: rgba(255,255,255, 0.05);
    }

    .form button{
        width: 300px !important;
        height: 40px !important;
        margin: 0 !important;
    }

    .form .cancel-button{
        text-align: center;
        text-decoration: underline;
        color: #ddd;
        font-size: 14px;
    }

    .form .cancel-button span{
        cursor: pointer;
    }

    .form .error-message{
        background: rgba(215, 67, 67, 0.2);
        width: 290px;
        color: #fff;
        font-size: 14px;
        padding: 4px;
        border: 1px solid rgba(215, 67, 67, 0.2);
        border-radius: 4px;
        text-align: center;
    }

    .form .message{
        width: 300px;
        color: rgba(255,255,255, 0.7);
        font-size: 12px;
    }
    .proxy-broken{
        border-color: #c52b2b !important;
    }
    .proxy-active{
        border-color: #2bc559 !important;
    }

    .proxy-input{
        display: flex;
        align-items: center;
        gap: 5px;
    }
    .proxy-input input{
        width: 265px;
        transition: border-color 0.5s linear;
    }
</style>