<template>
    <div class="progress-continer">
        <div class="progress-filler"></div>
    </div>
</template>

<script>
export default{
    data(){
        return {
            offset: "0px",
            prev_offset: 0,
            anim_time: "30ms",
            left: "0px"
        }
    },
    mounted(){
        this.timer(true);
    },
    methods:{
        timer(start = false){
            const time = Math.floor(Date.now()/1000);
            const _interval = Math.floor(time / 30)
            const end_time = (_interval + 1) * 30;
            const timeout = (end_time * 1000 - Date.now());
            const offset = Math.round(100 - timeout / 30000 * 100);
            if (start){
                this.offset = `-${offset}px`;
                setTimeout(this.timer, 200);
                return;
            }

            this.offset = `-100px`;
            this.anim_time = timeout+"ms"
            setTimeout(this.reset, timeout);
        },
        reset(){
            this.offset = "0px";
            this.anim_time = "0.3s"
            setTimeout(this.timer, 300)
        }
    }
}

</script>
<style scoped>
    .progress-continer{
        width: 100px;
        height: 4px;
        background: #2997f131;
        position: absolute;
        bottom: 0px;
        left: 0px;
        overflow: hidden;
    }
    .progress-filler{
        height: 4px;
        width: 100px;
        background: #2996F1;
        position: absolute;
        transition: all v-bind(anim_time) linear;
        transform: translateX(v-bind(offset));
        left: v-bind(left)
    }
</style>