<template>
    <div class="checkbox">
        <input type="checkbox" :id="checkbox_id" :checked="checked">
        <label :for="checkbox_id"></label>
    </div>
    
</template>

<script>

export default{
    props:['checked'],
    data(){
        return {
            checkbox_id: `checkbox-${Math.round(Math.random() * 10000)}`,
            ischecked: this.checked
        }
    }
}

</script>

<style scoped>
    .checkbox{
        display: flex;
        width: 30px;
        height: 30px;
        justify-content: center;
        align-items: center;
        border-radius: 30px;
    }
    .checkbox>input {
        display: none;
    }
    .checkbox:hover{
        background: rgba(255,255,255, 0.05);
    }

    input[type="checkbox"]:checked ~ label, 
    input[type="checkbox"]:not(:checked) ~ label {
        display: flex;
        color: #ddd;
        position: relative;
        cursor: pointer;
        align-items: center;
        height: 15px;
        width: 15px;
        border-radius: 2px;
    }
    input[type="checkbox"]:not(:checked) ~ label{
        border: 1px solid rgba(255,255,255, 0.1);
        box-sizing: border-box;
    }
    input[type="checkbox"]:checked ~ label{
        background: #2996F1;
    }

    input[type="checkbox"]:checked ~ label::before{
        content: " ";
        position: absolute;
        border-top: 1px solid #292929;
        width: 9px;
        transform: rotate(135deg);
        left: 5px;
        top: 7px;
    }
    input[type="checkbox"]:checked ~ label::after{
        content: " ";
        position: absolute;
        border-top: 1px solid #292929;
        width: 5px;
        transform: rotate(45deg);
        left: 2px;
        top: 9px;
    }
</style>