<template>
    <router-link :to="{path: '/id/'+account.id}" :class="is_active_class">
        <span v-html="login" :class="account.token_valid ? 'login' : 'login invalid'"></span>
        <div class="right-side">
            <span v-if="account.auto_conf" class="auto-conf">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><title>Auto Confirmation Active</title><path d="M12.68 6H11.32L7 16H9L9.73 14H14.27L15 16H17L12.68 6M10.3 12.5L12 8L13.7 12.5H10.3M17.4 20.4L19 22H14V17L16 19C18.39 17.61 20 14.95 20 12C20 7.59 16.41 4 12 4S4 7.59 4 12C4 14.95 5.61 17.53 8 18.92V21.16C4.47 19.61 2 16.1 2 12C2 6.5 6.5 2 12 2S22 6.5 22 12C22 15.53 20.17 18.62 17.4 20.4Z" /></svg>
            </span>
            <span v-if="account.proxy" :class="account.proxy_status ? 'proxy_active' : 'proxy_broken'"></span>
        </div>
    </router-link>
    
</template>

<script>
export default {
    props: ['account', 'search'],
    computed:{
        login(){
            if (!this.search) return this.account.login;
            const index = this.account.login.indexOf(this.search);
            return `${this.account.login.slice(0, index)}<u>${this.search}</u>${this.account.login.slice(index + this.search.length)}`
        },
        is_active_class(){
            return this.account.id == this.$route.params.id ? "active account" : "account"
        }
    }
}
</script>

<style scoped>
    .account{
        display: flex;
        flex-grow: 1;
        line-height: 45px;
        color: #cdcdcd;
        padding: 0 10px 0 20px;
        overflow: hidden;
        font-size: 12px;
        border-bottom: 1px solid rgba(255,255,255, 0.05);
        align-items: center;
        justify-content: space-between;
    }
    .account:hover, .active{
        background: rgba(255,255,255, 0.05);
        box-shadow: 0 3px 6px 0 rgba(0,0,0, 0.05);
    }
    .login{
        max-width: 130px;
        overflow-x: hidden;
    }

    .invalid{
        color: #c52b2b
    }


    .proxy_broken, .proxy_active{
        width: 10px;
        height: 10px;
        border-radius: 10px;
        display: inline-block;
    }
    .proxy_broken{
        background: #c52b2b;
    }
    .proxy_active{
        background: #2bc559;
    }
    .auto-conf{
        display: flex;
    }
    .auto-conf svg{
        width: 20px;
        height: 20px;
        filter: invert(100%) sepia(1%) saturate(4201%) hue-rotate(212deg) brightness(114%) contrast(73%);
    }
    .right-side{
        display: flex;
        align-items: center;
        gap: 5px;
    }

</style>