<template>
    <div class="conteiner">
        <LoadingAwait v-if="stage == 0" :size="'100px'"></LoadingAwait>

        <div v-if="stage == 1" class="form" @keypress.enter="dologin()">
            <div class="title">add account</div>
            <div v-if="error" class="error-message">{{ error }}</div>
            <input type="text" placeholder="Login" v-model="login">
            <input type="text" placeholder="Password" v-model="password">
            <SwitchBox @switch="use_proxy = !use_proxy">Proxy (http only)</SwitchBox>
            <input v-if="use_proxy" type="text" placeholder="[login:password@]IP:PORT" v-model="proxy">
            <div class="buttons">
                <button @click="dologin()">Login</button>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Cancel</span>
            </div>
        </div>

        <div v-if="stage == 1.1" class="form" @keypress.enter="send_guard()">
            <div class="title">Guard Email</div>
            <div v-if="error" class="error-message">{{ error }}</div>
            <div class="message">Account: {{ login }}</div>
            <input type="text" placeholder="Guard code" v-model.lazy="code_guard">
            <div class="buttons">
                <button @click="send_guard()">send</button>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Back</span>
            </div>
        </div>

        <div v-if="stage == 2" class="form" @keypress.enter="add_phone()">
            <div class="title">add phone</div>
            <div v-if="error" class="error-message">{{ error }}</div>
            <div class="message">There is no mobile phone number associated with your account. Do you want to link a number and receive SMS from Steam?</div>
            <SwitchBox @switch="want_add_phone = !want_add_phone">I want to add a phone number</SwitchBox>
            <div class="buttons">
                <button @click="add_phone(0)">send</button>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Cancel</span>
            </div>
        </div>

        <div v-if="stage == 2.1" class="form" @keypress.enter="add_phone()">
            <div class="title">add phone</div>
            <div v-if="error" class="error-message">{{ error }}</div>
            <div class="message">Account: {{ login }}<br>Enter phone number for add on your account</div>
            <input type="text" placeholder="+12345678901" v-model.lazy="phone_number">
            <div class="buttons">
                <button @click="add_phone()">send</button>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Cancel</span>
            </div>
        </div>

        <div v-if="stage == 2.2" class="form" @keypress.enter="add_phone(2)">
            <div class="title">email confirmation</div>
            <div v-if="error" class="error-message">{{ error }}</div>
            <div class="message">Steam has sent a confirmation to your email. Confirm adding phone number and click "Done"</div>
            <div class="buttons">
                <button @click="add_phone(2)">done</button>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Cancel</span>
            </div>
        </div>

        <div v-if="stage == 2.4" class="form" @keypress.enter="add_phone(4)">
            <div class="title">sms code</div>
            <div v-if="error" class="error-message">{{ error }}</div>
            <div class="message">Account: {{ login }}<br>Phone: {{ phone_number }}</div>
            <input type="text" placeholder="SMS code" v-model.lazy="phone_code">
            <div class="buttons">
                <button @click="add_phone(4)">send</button>
            </div>
            <div class="cancel-button" >
                <span @click="add_phone(3)">Resend SMS</span>
            </div>
            <div class="cancel-button" >
                <span @click="change_number()">Change number</span>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Cancel</span>
            </div>
        </div>

        <div v-if="stage == 3" class="form" @keypress.enter="twofa()">
            <div class="title">two factor auth</div>
            <div v-if="error" class="error-message">{{ error }}</div>
            <div v-if="want_add_phone" class="message">Now we will add two-factor authentication to your account. Click 'Send' for send SMS on account phone number. Last digits of your number: {{ last_phone_digits }}</div>
            <div v-else class="message">Now we will add two-factor authentication to your account. Click 'Send' for send Email with confirmation code</div>
            <div class="buttons">
                <button @click="twofa()">Send</button>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Cancel</span>
            </div>
        </div>

        <div v-if="stage == 3.01" class="form" @keypress.enter="stage = 3.02">
            <div class="title">{{ r_code }}</div>
            <div v-if="error" class="error-message">{{ error }}</div>
            <div class="message">Your revocation code is <b>{{ r_code }}</b>. Save it in a safe place. This code is used to restore access to your account</div>
            <div class="buttons">
                <button @click="stage = 3.02">I save it</button>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Cancel</span>
            </div>
        </div>

        <div v-if="stage == 3.02" class="form" @keypress.enter="check_rcode()">
            <div class="title">Enter your revocation code</div>
            <div v-if="error" class="error-message">{{ error }}</div>
            <input type="text" placeholder="R00000" v-model="write_r_code">
            <div class="buttons">
                <button @click="check_rcode()">Check</button>
            </div>
            <div class="cancel-button" >
                <span @click="stage = 3.01">Back</span>
            </div>
        </div>

        <div v-if="stage == 3.1" class="form" @keypress.enter="twofa(2)">
            <div class="title">Finish</div>
            <div v-if="error" class="error-message">{{ error }}</div>
            <input v-if="want_add_phone" type="text" placeholder="SMS code" v-model="sms_code">
            <input v-else type="text" placeholder="Email code" v-model="sms_code">
            <div class="buttons">
                <button @click="twofa(2)">Send</button>
            </div>
            <div class="cancel-button" >
                <span @click="set_stage(3)">Back</span>
            </div>
            <div class="cancel-button" >
                <span @click="back()">Cancel</span>
            </div>
        </div>

        <div v-if="stage == 4" class="form">
            <div class="title">success</div>
            <div v-if="!this.$route.query.account" class="buttons">
                <button @click="more()">add one more</button>
            </div>
            <div class="buttons">
                <button @click="exit()">exit</button>
            </div>
        </div>
    </div>
</template>

<script>

/**
 * Ввод пароля для аккаунтов утравших токен
 */

import SwitchBox from '@/components/SwitchBox.vue';
import LoadingAwait from '@/components/LoadingAwait.vue';

export default{
    components:{
        SwitchBox,
        LoadingAwait
    },
    data(){
        return {
            login: "",
            password: "",
            use_proxy: false,
            want_add_phone: false,
            proxy: "",
            code_guard: "",
            sms_code: "",
            r_code: "",
            error: "",
            steamID: this.$route.query.account ?? "",
            stage: 1,//this.$route.query.account ? 2 : 1,

            write_r_code: "",
            last_phone_digits: "",
            phone_code: "",
        }
    },
    mounted(){
        console.log(this.steamID)
    },
    methods:{
        back(){
            if (this.stage == 1.1){
                this.code_guard = "";
                this.set_stage(1);
                return
            }

            return this.$router.push({path: "/"});
        },
        check_rcode(){
            if (this.r_code == this.write_r_code) {
                this.set_stage(3.1)
            }
            else {
                this.error = "Wrong revocation code"
            }
        },
        async dologin(){
            if (!this.login) return this.error = "Enter login";
            if (!this.password) return this.error = "Enter password"
            let _proxy = this.proxy;
            if (this.proxy){
                try{
                    let proxy = this.proxy.includes("://") ? new URL(this.proxy) : new URL(`http://${this.proxy}`);
                    if (proxy.protocol !== "http:" && proxy.protocol !== "https:") return this.error = "Wrong proxy protocol. Use only HTTP"
                    _proxy = proxy.href;
                }catch(error){
                    return this.error = "Can't parse proxy. Check your input is correct"
                }
            }
            this.stage = 0;
            const result = await this.$message("add-new", {stage: 1, login: this.login, password: this.password, proxy: _proxy});
            if (!result.success){
                this.set_stage(1, result.error);
                return;
            }else{
                if (result.status == 2){
                    if (result.actions.includes('DeviceCode') || result.actions.includes('DeviceConfirmation')){
                        this.set_stage(1, "This account is already protected. Disable it to add an account here. Or import this account")
                    }else if (result.actions.includes('EmailCode')){
                        this.set_stage(1.1)
                    }
                }else this.login_success(); 
            }
        },
        async login_success(){
            const result = await this.$message("add-new", {stage: 2});
            if (result.has_phone){
                this.last_phone_digits = result.last_digits;
                this.want_add_phone = true;
                this.set_stage(3);
            }else{
                this.set_stage(2)
            }
        },
        async send_guard(){
            if (!this.code_guard) return this.error = "Enter guard code"
            this.stage = 0;
            const result = await this.$message("add-new", {stage: 1.1, code: this.code_guard});
            if (!result.success){
                if (result.error == "InvalidLoginAuthCode") this.set_stage(1.1, "Wrong code. Try again");
                else this.set_stage(1.1, result.error);
            } 
            else this.login_success();

        },
        async add_phone(step = 1){
            if (step == 0){
                if (this.want_add_phone) return this.stage = 2.1;
                else this.stage = 3;
            }

            if (step == 1){
                this.stage = 0;
                if (!this.phone_number) return this.error = "Enter phone number";
                const result = await this.$message("add-new", {stage: 2.1, number: this.phone_number});
                if (!result.success) return this.set_stage(2.1, result.error);
                else return this.set_stage(2.2);
            }else if(step == 2){
                this.stage = 0;
                const result = await this.$message("add-new", {stage: 2.2});
                if (!result.success) return this.set_stage(2.2, result.error);
                else return this.set_stage(2.4);
            }else if(step == 3){
                this.stage = 0;
                const result = await this.$message("add-new", {stage: 2.3});
                if (!result.success) return this.set_stage(2.3, result.error);
                else return this.set_stage(2.4);
            }else if(step == 4){
                if (!this.phone_code) return this.error = "Enter SMS code"
                this.stage = 0;
                const result = await this.$message("add-new", {stage: 2.4, code: this.phone_code})
                if (!result.success) return this.set_stage(2.4, result.error);
                else{
                    this.last_phone_digits = this.phone_number.slice(-2);
                    this.set_stage(3)
                }
            }
        },
        async twofa(step = 1){
            if (step == 1){
                this.stage = 0;
                const result = await this.$message("add-new", {stage: 3});
                if (!result.success) return this.set_stage(3, result.error);
                else {
                    this.r_code = result.r_code;
                    return this.set_stage(3.01);
                }
            }else if(step == 2){
                if (!this.sms_code) return this.error = "Enter SMS code";
                this.stage = 0;
                const result = await this.$message("add-new", {stage: 3.1, code: this.sms_code})
                if (!result.success) this.set_stage(3.1, result.error);
                else this.set_stage(4)
            }
        },
        send_sms(){
            this.stage = 0;
            setTimeout(()=>{
                this.stage = 3
            }, 1000)
        },
        finish(){
            this.stage = 0
            setTimeout(()=>{
                this.stage = 4
            }, 1000)
        },
        exit(){
            this.$router.back();
        },
        more(){
            this.login = ""
            this.password = ""
            this.use_proxy = false
            this.proxy = ""
            this.code_guard = ""
            this.r_code = ""
            this.steamID = ""
            this.phone_number = ""
            this.sms_code = ""

            this.write_r_code = ""
            this.last_phone_digits = ""
            this.phone_code = ""
            this.set_stage(1)
        },
        set_stage(stage, error = ""){
            this.error = error;
            this.stage = stage;
        },
        change_number(){
            this.phone_number = ""
            this.phone_code = ""
            this.set_stage(2.1)
        }
    }
}

</script>

<style scoped>
    .conteiner{
        display: flex;
        width: 800px;
        height: 550px;
        justify-content: center;
        align-items: center;
    }
    .form{
        display: flex;
        padding: 40px;
        background: rgba(255,255,255, 0.05);
        box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.2);
        flex-direction: column;
        gap: 20px;
    }
    .title{
        text-transform: uppercase;
        color: #ddd;
        font-size: 24px;
        text-align: center;
        letter-spacing: 0.05em;
        width: 300px;
    }
    .form input{
        width: 300px;
        height: 40px;
        outline: none;
        border: 1px solid rgba(255,255,255, 0.2);
        background: none;
        border-radius: 4px;
        box-sizing: border-box;
        color: #ddd;
        padding: 0 10px;
        letter-spacing: 0.05em;
    }

    .form input:hover, .form input:focus{
        background: rgba(255,255,255, 0.05);
    }

    .form button{
        width: 300px !important;
        height: 40px !important;
        margin: 0 !important;
    }

    .form .cancel-button{
        text-align: center;
        text-decoration: underline;
        color: #ddd;
        font-size: 14px;
    }

    .form .cancel-button span{
        cursor: pointer;
    }

    .form .error-message{
        background: rgba(215, 67, 67, 0.2);
        width: 290px;
        color: #fff;
        font-size: 14px;
        padding: 4px;
        border: 1px solid rgba(215, 67, 67, 0.2);
        border-radius: 4px;
        text-align: center;
    }

    .form .message{
        width: 300px;
        color: rgba(255,255,255, 0.7);
        font-size: 12px;
    }
</style>