<template>
    <div class="conteiner">
        <div v-if="await_load" class="loader">
            <LoadingAwait  :size="'100px'" :border="'5px'" :opt="0.8"></LoadingAwait>
        </div>
        <div v-else class="posts">
            <PostItem v-for="post of posts" :key="post.id" :post="post"></PostItem>
        </div>
    </div>
</template>

<script>
import PostItem from './PostItem.vue';
import LoadingAwait from '@/components/LoadingAwait.vue';

export default{
    data(){
        return {
            posts: [],
            await_load: true
        }
    },
    components:{
        PostItem,
        LoadingAwait
    },
    async mounted(){
        this.posts = await fetch("https://raw.githubusercontent.com/ost056/SteamAuthTool/main/posts.json").then(res=> res.json())
        this.await_load = false;
        //console.log(result)
    }
}

</script>

<style scoped>
.conteiner{
    display: flex;
    flex-direction: column;
    width: 600px;
    position: relative;
}

.posts{
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    gap: 20px;
    height: 510px;
    padding: 20px;
}
/* .posts::-webkit-scrollbar{
    display: none;
} */

.posts::-webkit-scrollbar {
  height: 5px;
  width: 6px;
}
.posts::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0.01);
}

.posts::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.1);
}

.posts::-webkit-scrollbar-thumb:hover {
    background-color: rgba(255, 255, 255, 0.15);
}

.posts::-webkit-scrollbar-thumb:active {
    background-color: rgba(255, 255, 255, 0.2);
}

.loader{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
}

</style>